
"use client";

import { useState } from "react";
import { useCart } from "@/context/cart-context";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import Image from "next/image";
import { Lock, Loader2 } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { getSupabase } from "@/lib/supabase";

export default function CheckoutPage() {
  const { cartItems, cartTotal, clearCart } = useCart();
  const { toast } = useToast();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const getImageUrl = (coverImageId: string) => {
    if (!coverImageId) return "";
    const supabase = getSupabase();
    const { data } = supabase.storage.from('ebook-covers').getPublicUrl(coverImageId);
    return data.publicUrl;
  }

  const handlePayment = async (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    if (!name || !email) {
      toast({
        title: "Missing Information",
        description: "Please enter your name and email.",
        variant: "destructive"
      });
      return;
    }
    setIsLoading(true);

    try {
      const response = await fetch('/api/paystack/initialize', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email,
          amount: Math.round(cartTotal * 100), // Amount in kobo/cents
          metadata: {
            name,
            cartItems: cartItems.map(item => ({ id: item.id, title: item.title, quantity: item.quantity })),
          },
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to initialize payment.');
      }

      // Redirect user to Paystack's payment page
      window.location.href = data.authorization_url;
      
      // Clear cart optimistically, or wait for webhook confirmation
      const purchaseHistory = cartItems.map(item => item.title).join(', ');
        if (typeof window !== 'undefined') {
            localStorage.setItem('purchaseHistory', purchaseHistory);
        }
       clearCart();


    } catch (error: any) {
      toast({
        title: "Payment Error",
        description: error.message || "An unexpected error occurred. Please try again.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8 md:py-12">
      <h1 className="font-headline text-3xl md:text-4xl font-bold mb-8 text-center">
        Checkout
      </h1>
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
        <div>
          <Card>
            <CardHeader>
              <CardTitle className="font-headline text-2xl">
                Billing Information
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Full Name</Label>
                  <Input id="name" placeholder="John Doe" required value={name} onChange={(e) => setName(e.target.value)} />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    type="email"
                    placeholder="you@example.com"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>

                <div className="pt-4">
                    <p className="text-sm text-muted-foreground flex items-center gap-1">
                      <Lock className="h-3 w-3" />
                      You will be redirected to Paystack to complete your payment.
                    </p>
                </div>

                <Button onClick={handlePayment} size="lg" className="w-full mt-6" disabled={cartItems.length === 0 || isLoading}>
                  {isLoading ? <Loader2 className="animate-spin" /> : `Pay GH₵${cartTotal.toFixed(2)}`}
                </Button>
              </form>
            </CardContent>
          </Card>
        </div>
        <div>
          <Card>
            <CardHeader>
              <CardTitle className="font-headline text-2xl">
                Order Summary
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {cartItems.map((item) => {
                   const imageUrl = getImageUrl(item.coverImageId);
                  return (
                    <div key={item.id} className="flex items-center gap-4">
                      <div className="relative w-16 h-24 rounded-md overflow-hidden">
                        {imageUrl && (
                            <Image
                            src={imageUrl}
                            alt={item.title}
                            fill
                            className="object-cover"
                            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                            />
                        )}
                      </div>
                      <div className="flex-grow">
                        <p className="font-semibold">{item.title}</p>
                        <p className="text-sm text-muted-foreground">
                          Quantity: {item.quantity}
                        </p>
                      </div>
                      <p className="font-medium">
                        GH₵{(item.price * item.quantity).toFixed(2)}
                      </p>
                    </div>
                  );
                })}
              </div>
              <Separator className="my-4" />
              <div className="space-y-2">
                <div className="flex justify-between">
                  <p>Subtotal</p>
                  <p>GH₵{cartTotal.toFixed(2)}</p>
                </div>
                <div className="flex justify-between">
                  <p>Tax</p>
                  <p>GH₵0.00</p>
                </div>
                <Separator className="my-2" />
                <div className="flex justify-between font-bold text-lg">
                  <p>Total</p>
                  <p>GH₵{cartTotal.toFixed(2)}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
